<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('COUNTRY_NAME') }}</label>
          <vs-input class="w-full" v-model="country.geoName"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('NUMERIC_CODE') }}</label>
          <vs-input class="w-full" v-model="country.geoCodeNumeric"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('ALPHA2_CODE') }}</label>
          <vs-input class="w-full" v-model="country.geoCodeAlpha2"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('ALPHA3_CODE') }}</label>
          <vs-input class="w-full" v-model="country.geoCodeAlpha3"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <vs-checkbox v-model="country.disabled">{{ $t('DISABLED') }}</vs-checkbox>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col w-full">
          <div class="flex justify-between">
            <vs-button class="px-5 py-3" color="primary" type="border" @click="cancel">{{ $t('CANCEL') }}</vs-button>
            <div class="dropdown-button-container">
              <vs-button class="btnx" type="filled" @click="modifyCountry('browse')">{{ $t('SUBMIT') }}</vs-button>
              <vs-dropdown vs-trigger-click>
                <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
                <vs-dropdown-menu>
                  <vs-dropdown-item @click="modifyCountry('stay')">
                    <span>{{ $t('SUBMIT_STAY') }}</span>
                  </vs-dropdown-item>
                  <vs-dropdown-item @click="modifyCountry('view')">
                    <span>{{ $t('SUBMIT_VIEW') }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>
      </div>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import GeoService from '@/services/commons/GeoService'

export default {
  name: 'country-modify',
  mixins: [AlertMixin],
  data: function () {
    return {
      country: {}
    }
  },
  methods: {
    async load () {
      let self = this
      GeoService.geos({
        geoId: this.$route.params.id,
        geoTypeEnumId: 'GEOT_COUNTRY'
      }).then(function (response) {
        self.country = response.data.geoList[0] || {}
        self.country.disabled = self.country.disabled === 'Y'
      })
    },
    modifyCountry (nextAction) {
      let self = this
      GeoService.modifyGeo({
        geoId: this.$route.params.id,
        geoTypeEnumId: 'GEOT_COUNTRY',
        geoName: this.country.geoName,
        geoCodeAlpha2: this.country.geoCodeAlpha2,
        geoCodeAlpha3: this.country.geoCodeAlpha3,
        geoCodeNumeric: this.country.geoCodeNumeric,
        disabled: this.country.disabled ? 'Y' : 'N'
      }).then(function (response) {
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewCountry', params: { id: response.data.geoId } })
          } else {
            self.$router.push({ name: 'BrowseCountries' })
          }
        }
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted: function () {
    this.load()
  }
}
</script>
